import { handleActions } from 'redux-actions'
import { UiState } from '../../types'
import {
  LOAD_WEATHER,
  LOAD_WEATHER_SUCCESS,
  loadWeatherSuccessActionType,
  loadWeatherActionType,
  loadCitiesSuccessActionType,
  LOAD_CITIES_SUCCESS,
  SET_OPEN_SEARCH,
  setOpenSearchActionType,
} from '../actions/ui'

const initialState: UiState = {
  cities: [],
  weather: {
    temp: null,
    wind: null,
  },
  city: null,
  openSearch: false,
}

const reducers = {
  [LOAD_WEATHER]: (state: UiState, { payload: name }: loadWeatherActionType): UiState => {
    return {
      ...state, city: name, weather: {
        temp: null,
        wind: null,
      },
    }
  },
  [LOAD_WEATHER_SUCCESS]: (state: UiState, { payload: weather }: loadWeatherSuccessActionType): UiState => {
    return { ...state, weather: { ...weather } }
  },
  [LOAD_CITIES_SUCCESS]: (state: UiState, { payload }: loadCitiesSuccessActionType): UiState => {
    return { ...state, cities: payload }
  },
  [SET_OPEN_SEARCH]: (state: UiState, { payload }: setOpenSearchActionType): UiState => {
    return { ...state, openSearch: payload }
  },
}

export const ui = handleActions<UiState, any>(reducers, initialState)
